<template>
  <div class="block1">
    <el-form>
      <el-form-item>
        <h1></h1>
      </el-form-item>

      <el-date-picker
        class="pot"
        v-model="datenumber"
        type="daterange"
        range-separator="~"
        start-placeholder="StartDate"
        end-placeholder="EndDate"
        format="yyyy-MM-dd"
        value-format="yyyy-MM-dd"
        @change="chooseTimeRange"
      ></el-date-picker>

      <el-button class="fis" icon="el-icon-search" type="primary" @click="getData()">Search</el-button>
      <el-form-item>
        <h1></h1>
      </el-form-item>
      <el-form-item>
        <ve-line
          :toolbox="toolbox"
          :data-empty="dataEmpty"
          :loading="loading"
          :data="chartData"
          :settings="chartSettings"
          :extend="extend"
          :legend="legend"
          :tooltip="tooltip"
          width="1440px"
          height="480px"
        ></ve-line>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import "v-charts/lib/style.css";
export default {
  data() {
    cc: 0;

    this.toolbox = {
      feature: {
        magicType: { type: ["line", "bar"] },
        saveAsImage: {}
      }
    };
    this.chartSettings = {
      // scale: [false, true],
      // xAxisType: 'time',
      labelMap: {
        date: "日期",
        min: "最低气温",
        max: "最高气温",
        avg: "平均气温"
      },
      // metrics: ["最高气温", "最低气温", "平均气温"],
      // stack: { 用户: ["最高气温", "最低气温", "平均气温"] },
      area: true
    };

    this.extend = {
      backgroundColor: "#FFFFFF",
      //x轴文本设置
      // "xAxis.0.axisLabel.rotate": 30, //x轴文本倾斜
      // 'xAxis.0.axisLine.lineStyle.color': "red",//x轴文本改变颜色
      "xAxis.0.axisLabel.color": "#E6A23C", //x轴文本颜色
      "xAxis.0.axisLabel.fontSize": 16, //x轴文本字体大小
      "xAxis.0.axisLabel.fontFamily": "Arial", //x轴文本字体系列
      "xAxis.0.axisLabel.fontWeight": "bold", //x轴文本字体粗细
      "xAxis.0.axisLabel.align": "center", //x轴文本字体位置

      //y轴文本设置
      // 'yAxis.0.axisLine.lineStyle.color': "green",//y轴文本改变颜色
      "yAxis.0.axisLabel.color": "#F56C6C", //y轴文本改变颜色
      "yAxis.0.axisLabel.fontSize": 16 //x轴文本字体大小
      // "yAxis.0.axisName": "单位°C" //x轴文本字体大小
    };

    this.tooltip = {
      trigger: "axis",
      backgroundColor: "#0D1B42", //鼠标移动到图上面时，显示的背景颜色
      padding: 15, //定义内边距
      formatter: function(params) {
        //自定义函数修改折线图给数据加单位
        // console.log(params);
        var str = ""; //声明一个变量用来存储数据
        str += "<div>" + params[0].name + "</div>"; //显示日期的函数
        for (var i = 0; i < params.length; i++) {
          // this.cc = params[i].data[1]
          // console.log(params[i].data)
          //图显示的数据较多时需要循环数据，这样才可以成功的给多条数据添加单位
          str +=
            '<div style="color:' +
            params[i].color +
            '"><span>' +
            params[i].seriesName +
            "</span> : <span>" +
            (params[i].data[1] ? params[i].data[1] + "°C" : "暂无") +
            "</span></div>";
        }
        return str;
      }
    };
    this.legend = {
      textStyle: {
        //图例文字的样式
        color: "#000000",
        fontSize: 17,
        fontWeight: "bold"
      }
    };

    // this.grid = {
    //   y: 60
    // };

    // printStr: "";

    return {
      s: "", //日期字符串
      s1: "",
      s2: "",
      dataEmpty: true,
      datenumber: null,
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      chartData: {
        columns: ["date", "min", "avg", "max"],

        rows: [
          // { seq: "2020/7/8", tmax: 12, tmin: 10, tavg: 11 },
          // { seq: "Day 2", tmax: 35, tmin: 32, tavg: 34 },
          // { seq: "Day 3", tmax: 29, tmin: 26, tavg: 24 },
          // { seq: "Day 4", tmax: 17, tmin: 14, tavg: 16 },
          // { seq: "Day 5", tmax: 37, tmin: 34, tavg: 35 },
          // { seq: "Day 6", tmax: 45, tmin: 42, tavg: 43 },
          // { seq: "Day 7", tmax: 38, tmin: 31, tavg: 36 }
        ]
      },
      loading: false
      // dataEmpty: false
    };
  },
  mounted() {
    // this.getData();
    this.dataEmpty = true;
  },
  methods: {
    getData() {
      this.dataEmpty = false;
      this.loading = true;
      console.log("getdata....");

      // this.$axios
      //   .post("/pickdate", {
      //     date1: this.s1, 
      //     date2: this.s2 
      //   })
      //   .then(res => {
      //     console.log(res.data);
      //     this.loading = false;
      //     this.chartData.rows = res.data;
      //   })
      //   .catch(failResponse => {});

      this.chartData.rows = [
        { 'date': "2020/7/8", 'min': 12, 'avg': 10, 'max': 11 },
        { date: "Day 2", min: 35, avg: 32, max: 34 },
        { date: "Day 3", min: 29, avg: 26, max: 24 },
        { date: "Day 4", min: 17, avg: 14, max: 16 },
        { date: "Day 6", min: 45, avg: 42, max: 43 },
        { date: "Day 7", min: 38, avg: 31, max: 36 }
      ];
      this.loading = false;
    },
    created() {
      this.getData();
    },
    chooseTimeRange(t) {
      this.s = t.join();
      const split = this.s.split(",");

      // console.log(split[0]); //打印第一个日期
      this.s1 = split[0];
      this.s2 = split[1];

      //   this.printStr = t;
    }
  }
};
</script>>
 <style>
.pot {
  top: 10px;
  left: 500px;
}
.fis {
  padding: 12px 10px;
  position: relative;
  top: -30px;
  left: 865px;
}
.block1 {
  line-height: 40px;
  width: 300px;
  height: 80px;
  color: white;
  /* position: absolute; */
  /* float: center; */
  /* background-color: #0c2152; */
}
</style>




